/* purgecss start ignore */

.lc-chart-label {
    display: flex;
    align-items: center;
    white-space: nowrap;
    font-size: 14px;
    line-height: 1.5;
    --tw-text-opacity: 1;
    color: rgba(var(--lc-color-black), var(--tw-text-opacity));
  }

.lc-chart-label-name {
      font-weight: 700;
    }

.lc-chart-label-color {
      display: inline-block;
      width: 11px;
      height: 11px;
      margin-right: 12px;
      border-radius: 100%;
    }

.lc-chart-label-value::before {
        content: ': ';
      }

.lc-chart-legend {
    display: flex;
    flex-flow: row wrap;
    list-style: none;
    padding: 16px 0;
    margin: -theme('spacing.4') -theme('spacing.16');
  }

.lc-chart-legend-item {
      padding: 4px 16px;
    }

.lc-chart-legend-item-label {
        font-weight: 400;
        --tw-text-opacity: 1;
        color: rgba(var(--lc-color-gray-dark), var(--tw-text-opacity));
      }

.lc-chart-legend-item-value {
        margin-left: 4px;
      }

.lc-chart-legend-item-button.lc-button {
          padding: 8px 16px;
          border-radius: 100px;
          border-width: 1px;
          border-style: solid;
          --tw-text-opacity: 1;
          color: rgba(var(--lc-color-gray-light), var(--tw-text-opacity));
          --tw-bg-opacity: 1;
          background-color: rgba(var(--lc-color-gray-lightest), var(--tw-bg-opacity));
          --tw-border-opacity: 1;
          border-color: rgba(var(--lc-color-gray-lightest), var(--tw-border-opacity));
        }

.lc-chart-legend-item-button.lc-button .lc-chart-label {
            color: inherit;
          }

.lc-chart-legend-item-button.lc-button .lc-button-content {
            display: flex;
            align-items: center;
          }

.lc-chart-legend-item-button.lc-button:hover {
            --tw-text-opacity: 1;
            color: rgba(var(--lc-color-gray), var(--tw-text-opacity));
            --tw-border-opacity: 1;
            border-color: rgba(var(--lc-color-gray-lighter), var(--tw-border-opacity));
          }

.lc-chart-legend-item-active .lc-chart-legend-item-button.lc-button {
        --tw-text-opacity: 1;
        color: rgba(var(--lc-color-gray-dark), var(--tw-text-opacity));
        background-color: transparent;
        --tw-border-opacity: 1;
        border-color: rgba(var(--lc-color-gray-lighter), var(--tw-border-opacity));
        box-shadow: 0px 8px 20px -12px rgba(0, 0, 0, 0.15);
      }

.lc-chart-legend-interactive {
      padding: 8px 0;
      margin: -theme('spacing.8') -theme('spacing.8');
    }

.lc-chart-legend-interactive .lc-chart-legend-item {
      padding: 8px 8px;
    }

.lc-chart-tooltip {
    position: absolute;
    pointer-events: none;
    transform: translate(-50%, -100%);
    transition: all 0.3s ease-in-out;
    opacity: 0;
  }

.lc-chart-tooltip-content {
      --tw-bg-opacity: 1;
      background-color: rgba(var(--lc-color-white), var(--tw-bg-opacity));
      font-size: 14px;
      line-height: 1.5;
      padding-top: 16px;
      padding-bottom: 16px;
      padding-left: 20px;
      padding-right: 20px;
      border-radius: 8px;
      border-width: 1px;
      --tw-border-opacity: 1;
      border-color: rgba(var(--lc-color-gray-lighter), var(--tw-border-opacity));
      text-align: center;
      box-shadow: 0px 12px 20px rgba(0, 0, 0, 0.1);
    }

.lc-chart-tooltip-loaded {
      opacity: 1;
    }

/* purgecss end ignore */
